import React from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import { signUp } from "../../actions";

class Register extends React.Component {

  onSubmit = formValues => {
    this.props.signUp(formValues);
  };

  renderInput = ({ input, label, meta }) => {
    const className = `field`;

    if (input.name === "password" || input.name === "password2") {
      return (
        <div className={className}>
          <label>{label}</label>
          <input {...input} autoComplete="off" type="password" required />
        </div>
      );
    }
    if (input.name === "email") {
      return (
        <div className={className}>
          <label>{label}</label>
          <input {...input} autoComplete="off" type="email" required />
        </div>
      );
    }
    return (
      <div className={className}>
        <label>{label}</label>
        <input {...input} autoComplete="off" required />
      </div>
    );

  };

  checkUser = () => {
    return (
      <div className="form-container">
        <form
          onSubmit={this.props.handleSubmit(this.onSubmit)}
          className="ui form"
        >
          <Field
            name="username"
            component={this.renderInput}
            label="Wprowadź login"
          />
          <Field
            name="email"
            component={this.renderInput}
            label="Wprowadź email"
          />
          <Field
            name="name"
            component={this.renderInput}
            label="Wprowadź imię"
          />
          <Field
            name="password"
            component={this.renderInput}
            label="Wprowadź hasło"
          />
          <Field
            name="password2"
            component={this.renderInput}
            label="Wprowadź hasło ponownie"
          />
          <button className="ui button">Zarejestruj</button>
        </form>
      </div>
    );
  };

  render = () => {
    if (this.props.user.isSigned) {
      return <div className="ui red message">Zalogowany</div>;
    } else {
      return <div id="register">{this.checkUser()}</div>;
    }
  };
}

const mapStateToProps = state => {
  return {
    user: state.user
  };
};

const formWrapped = reduxForm({
  form: "register"
})(Register);

export default connect(
  mapStateToProps,
  { signUp }
)(formWrapped);
