import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import {
  getMeals,
  getActivities,
  clearActivities,
  clearMeals,
  refreshMeals,
  getCounterMeals,
  getCounterActivities,
  deleteItem
} from "../../actions";
import history from "../../history";
import "react-datepicker/dist/react-datepicker.css";

import "react-widgets/dist/css/react-widgets.css";
import { DateTimePicker } from "react-widgets";

import Moment from "moment";
import momentLocalizer from "react-widgets-moment";

Moment.locale("en");
momentLocalizer();

const renderDateFormatJSON = date => {
  const day = date.getDate();
  const month = date.getMonth() + 1;
  const year = date.getFullYear();

  if (day < 10) {
    if (month < 10) {
      return year + "-0" + month + "-0" + day;
    }
    return year + "-" + month + "-0" + day;
  }

  if (month < 10) {
    if (day < 10) {
      return year + "-0" + month + "-0" + day;
    }
    return year + "-0" + month + "-" + day;
  }

  return year + "-" + month + "-" + day;
};

class All extends React.Component {
  state = {
    date: renderDateFormatJSON(new Date()),
    meals: null,
    activities: null,
    bmr: null,
    countermeals: {},
    counteractivities: {}
  };

  componentDidMount() {
    try {
      this.setState({ bmr: this.props.bmr.bmr.bmr });
    } catch (err) { }
    try {
      this.setState({
        counteractivities: this.props.counterActivities.counteractivities
      });
    } catch (err) { }
    try {
      this.setState({
        countermeals: this.props.counterMeals.countermeals
      });
    } catch (err) { }

    this.props.getMeals();
    this.props.getActivities();
    this.props.getCounterMeals(this.state.date);
    this.props.getCounterActivities(this.state.date);
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.meals !== this.props.meals) {
      try {
        this.setState({ meals: nextProps.meals[0].items });
      } catch (err) { }
    }
    if (nextProps.activities !== this.props.activities) {
      try {
        this.setState({ activities: nextProps.activities[0].items });
      } catch (err) { }
    }
    if (nextProps.counterMeals !== this.props.counterMeals) {
      try {
        this.setState({
          countermeals: nextProps.counterMeals.countermeals
        });
      } catch (err) { }
    }
    if (nextProps.counterActivities !== this.props.counterActivities) {
      try {
        this.setState({
          counteractivities: nextProps.counterActivities.counteractivities
        });
      } catch (err) { }
    }
  }

  renderAvg = () => {
    try {
      if (this.props.bmr.bmr.bmr !== null) {
        let result = 0;
        let days = 0;
        let meals = 0;
        let activities = 0;
        try {
          meals = this.state.countermeals.gcounter;
        } catch (err) { }

        try {
          activities = this.state.counteractivities.gcounter;
        } catch (err) { }

        try {
          days = this.state.countermeals.days;
        } catch (err) { }

        result = meals - activities;

        let kcal = result / days;

        let date = "";
        if (renderDateFormatJSON(new Date()) !== this.state.date) {
          date = `${this.state.date}/${renderDateFormatJSON(new Date())}`;
        } else {
          date = "dzisiaj";
        }

        return (
          <div className="data">
            <table className="ui celled table">
              <thead>
                <tr>
                  <th>Średni bilans dzienny: {date}</th>
                </tr>
              </thead>

              <tbody>
                <tr>
                  <td>{String(kcal)} kcal</td>
                </tr>
              </tbody>
            </table>
          </div>
        );
      }
    } catch (err) { }
  };

  onDateChange = date => {
    this.setState({ date: renderDateFormatJSON(date) });
    this.props.getCounterMeals(renderDateFormatJSON(date));
    this.props.getCounterActivities(renderDateFormatJSON(date));
  };

  onDateSelected() {
    return (

      <DateTimePicker
        value={new Date(this.state.date)}
        selected={new Date(this.state.date)}
        time={false}
        onChange={this.onDateChange}
      />

    );
  }

  renderItems = (item, text, text1, type) => {
    let counter = 0;
    try {
      let items = item.map(element => {
        if (String(element.date) === String(this.state.date)) {
          counter += parseInt(element.kcal);
          return element;
        }
        return undefined;
      });

      items = items.filter(function (el) {
        return el !== undefined;
      });

      if (items.length !== 0) {
        return this.renderTable(items, text, counter, text1);
      }
    } catch (err) { }
  };

  onElementClick = (element, text1) => {
    this.props.deleteItem(element, text1);
  };

  renderTable = (items, text, counter, text1) => {
    let renderitems = items.map(element => {
      try {
        return (
          <tr
            key={element._id}
            onClick={this.onElementClick.bind(this, element, text1)}
          >
            <th>{element.name}</th>
            <td>{element.kcal} kcal</td>
            <td>{element.description}</td>
          </tr>
        );
      } catch (err) {
        return "";
      }
    });

    return (
      <div className="data">
        <table className="ui celled striped  table">

          <thead>
            <tr>
              <th>{text1}</th>
            </tr>
          </thead>

          <tbody>
            <tr>
              <td>{counter} kcal</td>
            </tr>
          </tbody>
        </table>

        <br />
        <table className="ui celled table meals">
          <thead>
            <tr>
              <th></th>
              <th>{text}</th>
              <th></th>
            </tr>
          </thead>

          <tbody>{renderitems}</tbody>
        </table>
      </div>
    );
  };

  renderBMR = () => {
    try {
      if (this.props.bmr.bmr.bmr !== null) {
        return (
          <div className="data">
            <table className="ui celled table bilance">
              <thead>
                <tr>
                  <th>BMR</th>
                </tr>
              </thead>

              <tbody>
                <tr>{this.renderbmr()}</tr>
              </tbody>
            </table>
          </div>
        );
      }
    } catch (err) { }
  };

  renderbmr = () => {
    if (this.props.bmr.bmr.bmr !== null) {
      return <td>{this.props.bmr.bmr.bmr}</td>;
    }
  };

  render = () => {
    if (this.props.user.isSigned) {
      return (
        <div className="main">
          <div id="my-buttons">
            <Link to="/addmeal" className="ui button">
              Dodaj posiłek
            </Link>

            <Link to="/addactivity" className="ui button">
              Dodaj aktywność
            </Link>
          </div>

          <div id="raport">
            {this.renderBMR()}
            <br />
            {this.onDateSelected()}
            <br />
            {this.renderAvg()}
            <br />
            {this.renderItems(
              this.state.meals,
              "Posiłki",
              "Zjedzone Kalorie:",
              "meals"
            )}

            {this.renderItems(
              this.state.activities,
              "Aktywności",
              "Spalone Kalorie:",
              "activities"
            )}

          </div>
        </div>
      );
    } else {
      history.push("/login");
      return <div>Niezalogowany</div>;
    }
  };
}

const mapStateToProps = state => {
  return {
    user: state.user,
    meals: Object.values(state.meals),
    activities: Object.values(state.activities),
    bmr: state.bmr,
    counterMeals: state.countermeals,
    counterActivities: state.counteractivities
  };
};

export default connect(
  mapStateToProps,
  {
    getMeals,
    getActivities,
    clearActivities,
    clearMeals,
    refreshMeals,
    getCounterMeals,
    getCounterActivities,
    deleteItem
  }
)(All);
