import {
  SIGN_UP,
  SIGN_UP_PROBLEM,
  SIGN_IN,
  SIGN_IN_PROBLEM,
  LOG_OUT,
  TOKEN
} from "../actions/types";

const INITIAL_STATE = {
  user: JSON.parse(localStorage.getItem("user")),
  isSigned: JSON.parse(localStorage.getItem("isSigned")),
  token: JSON.parse(localStorage.getItem("token"))
};

export default (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case SIGN_UP:
      return { ...state, registration: action.payload };

    case SIGN_UP_PROBLEM:
      return { ...state, errors: action.payload };

    case SIGN_IN:
      return { ...state, user: action.payload, isSigned: true };

    case SIGN_IN_PROBLEM:
      return { ...state, errors: action.payload };

    case LOG_OUT:
      return { ...state, user: INITIAL_STATE, isSigned: false };

    case TOKEN:
      return { ...state, token: action.payload };

    default:
      return state;
  }
};
