import {
  GET_ACTIVITIES,
  ADD_ACTIVITY,
  CLEAR_ACTIVITIES,
  REFRESH_ACTIVITIES,
  DELETE_ACTIVITY
} from "../actions/types";

const INITIAL_STATE = {
  activities: JSON.parse(localStorage.getItem("activities"))
};

export default (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case GET_ACTIVITIES:
      return { ...state, activities: action.payload };

    case ADD_ACTIVITY:
      return {
        ...state,
        activities: action.payload
      };

    case CLEAR_ACTIVITIES:
      return { ...state, activities: null };

    case REFRESH_ACTIVITIES:
      return { ...state, INITIAL_STATE };

    case DELETE_ACTIVITY:
      return {
        ...state,
        activities: {
          ...state.activities,
          items: state.activities.items.filter(
            activity => activity._id !== action.payload
          )
        }
      };

    default:
      return state;
  }
};
