import {
  GET_MEALS,
  ADD_MEAL,
  CLEAR_MEALS,
  REFRESH_MEALS,
  DELETE_MEAL
} from "../actions/types";

const INITIAL_STATE = {
  meals: JSON.parse(localStorage.getItem("meals"))
};

export default (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case GET_MEALS:
      return { ...state, meals: action.payload };

    case ADD_MEAL:
      return {
        ...state,
        meals: action.payload
      };

    case CLEAR_MEALS:
      return { ...state, meals: null };

    case REFRESH_MEALS:
      return { ...state, INITIAL_STATE };

    case DELETE_MEAL:
      return {
        ...state,
        meals: {
          ...state.meals,
          items: state.meals.items.filter(meal => meal._id !== action.payload)
        }
      };

    default:
      return state;
  }
};
