import { GET_COUNTER_MEALS,REFRESH_COUNTER_MEALS } from "../actions/types";

const INITIAL_STATE = {
  countermeals: JSON.parse(localStorage.getItem("countermeals"))
};

export default (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case GET_COUNTER_MEALS:
      return { ...state, countermeals: action.payload };

    case REFRESH_COUNTER_MEALS:
      return { ...state, countermeals:action.payload };

    default:
      return state;
  }
};
