import api from "../apis/api";
import {
  GET_MEALS,
  SIGN_UP,
  SIGN_IN,
  LOG_OUT,
  ADD_MEAL,
  ADD_ACTIVITY,
  GET_ACTIVITIES,
  CLEAR_ACTIVITIES,
  CLEAR_MEALS,
  REFRESH_MEALS,
  REFRESH_ACTIVITIES,
  BMR,
  GET_COUNTER_MEALS,
  GET_COUNTER_ACTIVITIES,
  DELETE_MEAL,
  DELETE_ACTIVITY,
  REFRESH_COUNTER_MEALS,
  REFRESH_COUNTER_ACTIVITIES,
  TOKEN
} from "./types";

import history from "../history";
import { hideLoader, showLoader } from "../helpers"

export const deleteItem = (element, text) => async (dispatch, getState) => {
  if (getState().user.isSigned) {
    let el = { id: element._id };
    const id = { id: getState().user.user.userId };

    if (text === "Zjedzone Kalorie:") {
      showLoader();
      await api.delete(
        `/meals/delete/${el.id}/${id.id}/${getState().user.token}`
      );
      hideLoader();
      let countermeals = JSON.parse(localStorage.getItem("countermeals"));

      countermeals.gcounter =
        parseInt(countermeals.gcounter) - parseInt(element.kcal);
      localStorage.setItem("countermeals", JSON.stringify(countermeals));

      dispatch({ type: REFRESH_COUNTER_MEALS, payload: countermeals });
      try {
        dispatch({ type: DELETE_MEAL, payload: element._id });
      } catch (err) { }
    }

    if (text === "Spalone Kalorie:") {
      showLoader();
      await api.delete(
        `/activities/delete/${el.id}/${id.id}/${getState().user.token}`
      );
      hideLoader();
      let counteractivities = JSON.parse(
        localStorage.getItem("counteractivities")
      );

      counteractivities.gcounter =
        parseInt(counteractivities.gcounter) - parseInt(element.kcal);
      localStorage.setItem(
        "counteractivities",
        JSON.stringify(counteractivities)
      );

      dispatch({ type: REFRESH_COUNTER_ACTIVITIES });

      dispatch({ type: DELETE_ACTIVITY, payload: element._id });
    }
  }
};

export const getCounterMeals = date => async (dispatch, getState) => {
  if (getState().user.isSigned) {
    const id = { id: getState().user.user.userId };
    showLoader();
    const response = await api.post("/meals/getCounter", {
      id: id,
      date: date,
      token: getState().user.token
    });
    hideLoader();
    localStorage.setItem(
      "countermeals",
      JSON.stringify({
        counter: response.data.counter,
        gcounter: response.data.gcounter,
        days: response.data.days,
        name: "countermeals"
      })
    );

    dispatch({
      type: GET_COUNTER_MEALS,
      payload: {
        counter: response.data.counter,
        gcounter: response.data.gcounter,
        days: response.data.days,
        name: "countermeals"
      }
    });
  }
};

