export const getMeals = () => async (dispatch, getState) => {
  if (getState().user.isSigned) {
    const id = { id: getState().user.user.userId };
    showLoader();
    const response = await api.post("/meals", {
      id,
      token: getState().user.token
    });
    hideLoader();
    localStorage.setItem("meals", JSON.stringify(response.data));

    dispatch({ type: GET_MEALS, payload: response.data });
  }
};

export const addMeal = formValues => async (dispatch, getState) => {
  if (getState().user.isSigned) {
    const id = { id: getState().user.user.userId };
    try {
      showLoader();
      const response = await api.post("/meals/add", {
        ...formValues,
        id,
        token: getState().user.token
      });
      hideLoader();
      let meals = JSON.parse(localStorage.getItem("meals"));
      if (meals === null) {
        localStorage.setItem("meals", JSON.stringify(response.data));
      } else {
        meals.items.push(response.data);
        localStorage.setItem("meals", JSON.stringify(meals));
      }

      dispatch({
        type: ADD_MEAL,
        payload: meals
      });
      history.push("/");
    } catch (err) {
      hideLoader();
      alert("Wystąpił problem z wyborem daty");
    }
  }
};

