export const signUp = formValues => async dispatch => {
  document.getElementById("All").style.display = "none";
  document.getElementById("loader").style.display = "block";
  let response = await api.post("/users/register", formValues);
  document.getElementById("All").style.display = "block";
  document.getElementById("loader").style.display = "none";
  dispatch({ type: SIGN_UP, payload: response.data });
  history.push("/login");
};

export const bmr = formValues => async (dispatch, getState) => {
  if (getState().user.isSigned) {
    const _id = { _id: getState().user.user.userId };
    showLoader();
    let response = await api.post("/users/bmr", {
      ...formValues,
      _id,
      token: getState().user.token
    });
    hideLoader();
    let user = JSON.parse(localStorage.getItem("user"));

    let bmr = {
      id: user.userId,
      height: response.data.height,
      weight: response.data.weight,
      activity: response.data.activity,
      sex: response.data.sex,
      age: response.data.age,
      bmr: response.data.bmr
    };

    localStorage.setItem("bmr", JSON.stringify(bmr));

    dispatch({
      type: BMR,
      payload: bmr
    });

    history.push("/");
  }
};

export const signIn = formValues => async dispatch => {
  try {
    showLoader();
    let response = await api.post("/users/login", formValues);

    hideLoader();

    let res = {
      userId: response.data.user._id,
      username: response.data.user.username,
      email: response.data.user.email
    };

    let bmr = {
      id: response.data.user._id,
      height: response.data.user.height,
      weight: response.data.user.weight,
      activity: response.data.user.activity,
      sex: response.data.user.sex,
      age: response.data.user.age,
      bmr: response.data.user.bmr
    };

    localStorage.setItem("bmr", JSON.stringify(bmr));
    localStorage.setItem("user", JSON.stringify(res));
    localStorage.setItem("token", JSON.stringify(response.data.user.token));
    localStorage.setItem("isSigned", true);

    dispatch({
      type: BMR,
      payload: bmr
    });

    dispatch({
      type: TOKEN,
      payload: response.data.user.token
    });

    dispatch({
      type: SIGN_IN,
      payload: res
    });

    history.push("/");
  } catch (err) {
    alert("UPS... Coś poszło nie tak..");
  }
};

export const clearMeals = () => {
  localStorage.clear();
  localStorage.setItem("meals", null);

  return {
    type: CLEAR_MEALS,
    payload: null
  };
};

export const clearActivities = () => {
  localStorage.clear();
  localStorage.setItem("activities", null);

  return {
    type: CLEAR_ACTIVITIES,
    payload: null
  };
};

export const refreshMeals = () => {
  history.push("/");

  return {
    type: REFRESH_MEALS
  };
};

export const refreshActivities = () => {
  history.push("/");

  return {
    type: REFRESH_ACTIVITIES
  };
};

export const logOut = () => async (dispatch, getState) => {
  if (getState().user.isSigned) {
    const id = { id: getState().user.user.userId };
    showLoader();
    await api.post("/users/logout", { id });
    hideLoader();
    localStorage.clear();
    localStorage.setItem("user", null);
    localStorage.setItem("isSigned", false);

    dispatch({
      type: LOG_OUT
    });

    history.push("/login");
  }
};
