import React from "react";
import { Field, reduxForm, change } from "redux-form";
import { connect } from "react-redux";
import { bmr } from "../../actions";
import history from "../../history";

class Bmr extends React.Component {
  componentDidMount() {
    if (this.props.user.isSigned) {
      this.props.initialize({
        weight: this.props.bmR.bmr.weight,
        height: this.props.bmR.bmr.height,
        activity: this.props.bmR.bmr.activity,
        sex: this.props.bmR.bmr.sex,
        age: this.props.bmR.bmr.age
      });
      this.props.dispatch(change("bmr", "anotherField", "value"));
    }
  }

  onSubmit = formValues => {
    this.props.bmr(formValues);
  };

  renderInput = ({ input, label, meta }) => {
    const className = `field`;

    return (
      <div className={className}>
        <label>{label}</label>
        <input type="text" {...input} autoComplete="off" />
      </div>
    );
  };

  renderActivitySelect = ({ input, label, meta }) => {
    const className = `field`;

    return (
      <div className={className}>
        <label>{label}</label>
        <select {...input}>
          <option />
          <option value="1.1">1.1</option>
          <option value="1.2">1.2</option>
          <option value="1.3">1.3</option>
          <option value="1.4">1.4</option>
          <option value="1.5">1.5</option>
          <option value="1.6">1.6</option>
          <option value="1.7">1.7</option>
          <option value="1.8">1.8</option>
          <option value="1.9">1.9</option>
          <option value="2.0">2.0</option>
        </select>
      </div>
    );
  };

  renderSexSelect = ({ input, label, meta }) => {
    const className = `field`;

    return (
      <div className={className}>
        <label>{label}</label>
        <select {...input}>
          <option />
          <option value="m">mężczyzna</option>
          <option value="k">kobieta</option>
        </select>
      </div>
    );
  };

  checkUser = () => {
    return (
      <div className="form-container">
        <form
          onSubmit={this.props.handleSubmit(this.onSubmit)}
          className="ui form"
        >
          <Field
            name="weight"
            component={this.renderInput}
            label="Aktualna waga"
          />
          <Field
            name="height"
            component={this.renderInput}
            label="Aktualny wzrost"
          />
          <Field
            name="activity"
            component={this.renderActivitySelect}
            label="Współczynnik aktywności"
          />

          <Field name="age" component={this.renderInput} label="Twój wiek" />
          <Field name="sex" component={this.renderSexSelect} label="Płeć" />

          <button className="ui button">Zatwierdź</button>
        </form>
      </div>
    );
  };

  render = () => {
    if (this.props.user.isSigned) {
      return <div id="bmr">{this.checkUser()}</div>;
    } else {
      history.push("/login");
      return <div>Niezalogowany</div>;
    }
  };
}

const mapStateToProps = state => {
  return {
    user: state.user,
    bmR: state.bmr
  };
};

const formWrapped = reduxForm({
  form: "bmr"
})(Bmr);

export default connect(
  state => mapStateToProps,
  {
    bmr,
    initialValues: {
      weight: bmr.weight,
      height: bmr.height,
      activity: bmr.activity,
      sex: bmr.sex,
      age: bmr.age
    }
  }
)(formWrapped);
