import React from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import { signIn, getMeals, getActivities } from "../../actions";
import { Link } from "react-router-dom";

class Login extends React.Component {

  onSubmit = formValues => {
    this.props.signIn(formValues);
  };

  renderInput = ({ input, label, meta }) => {
    const className = `field`;

    if (input.name === "password") {
      return (
        <div className={className}>
          <label>{label}</label>
          <input {...input} autoComplete="off" type="password" required/>
        </div>
      );
    } else {
      return (
        <div className={className}>
          <label>{label}</label>
          <input {...input} autoComplete="off" required/>
        </div>
      );
    }
  };

  checkUser = () => {
    return (
      <div className="column form-container">
        <form
          onSubmit={this.props.handleSubmit(this.onSubmit)}
          className="ui form"
        >
          <Field
            name="username"
            component={this.renderInput}
            label="Wprowadź login"
          />
          <Field
            name="password"
            component={this.renderInput}
            label="Wprowadź hasło"
          />

          <button className="ui button">Zaloguj</button>

        </form>
      </div>
    );
  };

  render = () => {
    return (
      <div id="login">{this.checkUser()}</div>
    );
  };
}

const mapStateToProps = state => {
  return {
    user: state.user
  };
};

const formWrapped = reduxForm({
  form: "login"
})(Login);

export default connect(
  mapStateToProps,
  { signIn, getMeals, getActivities }
)(formWrapped);
