import {
  GET_COUNTER_ACTIVITIES,
  REFRESH_COUNTER_ACTIVITIES
} from "../actions/types";

const INITIAL_STATE = {
  counteractivities: JSON.parse(localStorage.getItem("counteractivities"))
};

export default (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case GET_COUNTER_ACTIVITIES:
      return { ...state, counteractivities: action.payload };

    case REFRESH_COUNTER_ACTIVITIES:
      return { ...state, counteractivities: action.payload };

    default:
      return state;
  }
};
