import React from "react";
import All from "./All/All";
import AddMeal from "./Meals/AddMeal";
import Register from "./Sign/Register";
import Login from "./Sign/Login";
import Header from "./Header/Header";
import AddActivity from "./Activities/AddActivity";
import Bmr from "./Bmr/Bmr";

import { Route, HashRouter } from "react-router-dom";

import history from "../history";

class App extends React.Component {
  render() {
    return (
      <div id="App">

        <div className="ui active inverted dimmer" id="loader">
          <div className="ui massive text loader">Ładowanie...</div>
        </div>

        <div id="All">
          <HashRouter history={history}>
            <Header />
            <Route path="/" exact component={All} />
            <Route path="/register" exact component={Register} />
            <Route path="/login" exact component={Login} />
            <Route path="/addmeal" exact component={AddMeal} />
            <Route path="/addactivity" exact component={AddActivity} />
            <Route path="/bmr" exact component={Bmr} />
          </HashRouter>
        </div>
      </div>
    );
  }
}

export default App;
