import { combineReducers } from "redux";
import { reducer as formReducer } from "redux-form";

import mealsReducer from "./mealsReducer";
import userReducer from "./userReducer";
import activitiesReducer from "./activitiesReducer";
import bmrReducer from "./bmrReducer";
import counterMealsReducer from "./counterMealsReducer";
import counterActivitiesReducer from "./counterActivitiesReducer";

export default combineReducers({
  meals: mealsReducer,
  form: formReducer,
  user: userReducer,
  bmr: bmrReducer,
  countermeals: counterMealsReducer,
  counteractivities: counterActivitiesReducer,
  activities: activitiesReducer
});
