import React from "react";
import { connect } from "react-redux";
import { addMeal } from "../../actions";
import history from "../../history";
import { Field, reduxForm } from "redux-form";
import "react-widgets/dist/css/react-widgets.css";
import { DateTimePicker } from "react-widgets";

import Moment from "moment";
import momentLocalizer from "react-widgets-moment";

Moment.locale("en");
momentLocalizer();

class AddMeal extends React.Component {
  state = { startDate: new Date() };
  componentDidMount() { }

  onSubmit = formValues => {
    this.props.addMeal(formValues);
  };

  renderInputDate = ({ input: { onChange }, label, meta }) => {
    const className = `field`;

    return (
      <div className={className}>
        <label>{label}</label>
        <DateTimePicker time={false} onChange={onChange} />
      </div>
    );
  };

  renderInput = ({ input, label, meta }) => {
    const className = `field`;
    if (input.name === "kcal") {
      return (
        <div className={className}>
          <label>{label}</label>
          <input {...input} autoComplete="off" type="number" required />
        </div>
      );
    }

    return (
      <div className={className}>
        <label>{label}</label>
        <input {...input} autoComplete="off" />
      </div>
    );
  };

  renderMealType = ({ input, label, meta }) => {
    const className = `field`;

    return (
      <div className={className}>
        <label>{label}</label>
        <select {...input}>
          <option value="śniadanie" />
          <option value="śniadanie">śniadanie</option>
          <option value="lunch">lunch</option>
          <option value="obiad">obiad</option>
          <option value="podwieczorek">podwieczorek</option>
          <option value="kolacja">kolacja</option>
          <option value="przekąska">przekąska</option>
        </select>
      </div>
    );
  };

  checkUser = () => {
    return (
      <div className="form-container">
        <form
          onSubmit={this.props.handleSubmit(this.onSubmit)}
          className="ui form"
        >
          <Field
            name="meal"
            component={this.renderMealType}
            label="Wprowadź posiłek"
          />
          <Field
            name="kcal"
            component={this.renderInput}
            label="Zjedzone kalorie"
          />
          <Field
            name="date"
            showTime={false}
            component={this.renderInputDate}
            label="Wprowadź datę"
          />
          <Field
            name="description"
            component={this.renderInput}
            label="Wprowadź opis"
          />

          <button className="ui button">Dodaj</button>
        </form>
      </div>
    );
  };

  render = () => {
    if (this.props.user.isSigned) {
      return <div id="meals">{this.checkUser()}</div>;
    } else {
      history.push("/login");
      return <div>Niezalogowany</div>;
    }
  };
}

const mapStateToProps = state => {
  return {
    user: state.user,
    meals: Object.values(state.meals)
  };
};

const formWrapped = reduxForm({
  form: "addmeal"
})(AddMeal);

export default connect(
  mapStateToProps,
  { addMeal }
)(formWrapped);
