import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { logOut } from "../../actions";

class Header extends React.Component {
  onLogOutClick = () => {
    this.props.logOut();
  };

  componentDidMount() { }

  componentWillReceiveProps(nextProps) { }

  onLogOff = () => {
    return (
      <div className="header-panel">
        <div className="logo"><i className=" apple icon large"></i>FIT APP</div>
        <div className="ui secondary pointing menu">
          <Link to="/login" className="item">
            Logowanie
        </Link>
          <Link to="/register" className="item">
            Rejestracja
        </Link>
        </div>
      </div>
    );
  };

  onLogIn = () => {
    return (
      <div className="header-panel">
        <div className="logo"><i className=" apple icon large"></i>FIT APP</div>
        <div className="ui secondary pointing menu">
          <Link to="/" className="item">
            Raport
        </Link>

          <div className="right menu">
            <Link to="/bmr" className="item">
              Bmr
        </Link>

            <Link
              to="/login"
              className="item"
              onClick={this.onLogOutClick}
            >
              Wyloguj({this.props.user.user.username})
        </Link>
          </div>
        </div>
      </div>
    );
  };

  checkLogStatus = () => {
    return this.props.user.isSigned ? this.onLogIn() : this.onLogOff()
  };

  render = () => {
    return <div className="header">{this.checkLogStatus()}</div>
  };
}

const mapStateToProps = state => {
  return {
    user: state.user,
    meals: state.meals,
    activities: state.activities
  };
};

export default connect(
  mapStateToProps,
  { logOut }
)(Header);
